import './css/club.css'
import { Container, Row, Col, Card, } from "react-bootstrap";
import { useNavigate } from 'react-router-dom';

function Club({toUclubs}){    

    return(
        <div className="club">
            <header className="clubHeader">
                <h3>To.U CLUB beta</h3>
                <h5>      </h5>
            </header>
            <section className='clubSection'>
                <div className='clubSection1'>
                <h3>2U</h3>
                <h5>      !</h5>
                <p>   ,     <br/>
                           .<br/>
                       ,      ,<br/>
                      !
                </p>
                </div>
                <div className='clubSection2'>

                <Container>
                <div className='clubContainer'>
                <Row sm={1} md={4}>
                {toUclubs.map((data, i)=>{
                    return(
                        <div className='clubCard'><ClubItem key={data.id} toUclubs={toUclubs} toUclub={data} i={i}/></div>
                    )
                })}</Row>
                </div>
                </Container>
                </div>
            </section>

        </div>
    );
} export default Club;


function ClubItem({toUclubs, toUclub, i}){

    let navigate = useNavigate();

    return(
    <div>
        <Col>
            <Card onClick={()=>{navigate("./"+toUclub.id) }}>
            <Card.Img variant="top" src={toUclubs[i].imgPath}/>
            <Card.Body>
                <Card.Title>{toUclub.clubName}</Card.Title>
                <Card.Text>
                {": "}{toUclub.clubPrice}{""}
                </Card.Text>
            </Card.Body>
            </Card>
        </Col>
    </div>
    );
}